import React, { useContext } from 'react';
import {AuthContext} from "../../context/AuthProvider";
import { Link } from 'react-router-dom';

import './navbar.css';


const Navbar = () => {
    const authContext = useContext(AuthContext);

    const logoutHandler = () => {
        if (window.confirm("Você tem certeza de que deseja ecerrar essa sessão?")) {
            authContext.logout();
        } 
    };      

    function togglerNavHandler(e) {
        var element = document.getElementById("navbarNavDropdown");
        element.className = "collapse navbar-collapse";
    }

  return (
    <nav className="navbar navbar-expand-lg fixed-top">
        <Link className="navbar-brand" to="/">
            <img src="/assets/img/logo.png" width="60" height="60" className="d-inline-block align-top" alt="" />
        </Link>
        <button className="navbar-toggler navbar-dark" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavDropdown">
            <ul className="navbar-nav mx-auto">
                {/* <li className="nav-item active">
                    <a className="nav-link" href="#">Home <span className="sr-only">(current)</span></a>
                </li> */}
                <li className="nav-item">
                    <Link className="nav-link" to='/#home' onClick={togglerNavHandler}>Brilho</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to='support' onClick={togglerNavHandler}>Suporte e Documentação</Link>
                </li>
                <li>
                    <Link className="nav-link" to='about' onClick={togglerNavHandler}>Sobre</Link>
                </li>
            </ul>
            { authContext.isLoggedIn ? (
                <ul className="navbar-nav">
                    <li><Link className="nav-link" to='profile' onClick={togglerNavHandler}>Perfil</Link></li>
                    <li><button className='btn btn-secondary logout' onClick={logoutHandler}>Sair</button></li>
                </ul>
            ) : (
                <ul className="navbar-nav">
                    <li><Link className="nav-link" to='register' onClick={togglerNavHandler}>Registrar-se</Link></li>
                </ul>
            )}
        </div>
    </nav>
  )
}

export default Navbar
