import React, { useState, useContext } from "react";
import { useNavigate } from "react-router";
import { AuthContext } from "../../context/AuthProvider";
import axios from "axios";

import "./login.css";

function initialState() {
  return { email: "", password: "" };
}

const Login = () => {
  const authContext = useContext(AuthContext);
  const [values, setValues] = useState(initialState);

  const navigate = useNavigate();

  function onChange(e) {
    const { value, name } = e.target;

    setValues({
      ...values,
      [name]: value,
    });
  }

  function onSubmit(e) {
    e.preventDefault();

    axios
      .post("users/login", values)
      .then((resp) => {
        const { success, token, userInfo, code } = resp?.data;

        if (!success) {
          if (code === 1) return alert("E-mail não encontrado. Por favor, tente novamente ou cadastre.");
          if (code === 2) return alert("Ops.. Você errou sua senha. Por favor, tente novamente..");
          if (code === 3) return alert("Não foi possível realizar a requisição. Tente novamente em alguns instantes.")
        };

        if (success) {
          authContext.login(token, userInfo);
          navigate("/");
          alert("Bem vindo de volta, " + userInfo.user);
        }
      })
      .catch((error) => {
        alert("Não foi possível realizar a requisição. Tente novamente em alguns instantes.");
        console.log(error);
      });

    setValues(initialState);
  }

  return (
        <div className="signin-body">
          <div className="container signin-container">
            <div className="row">
              <div className="col"></div>
              <div className="col-sm-12 col-md-8">
                <div className="card signin-card">
                  <div className="card-body">
                    <img
                      src="assets/img/logo.png"
                      className="img-fluid signin-img"
                      alt=""
                    />

                    <form className="signin-form" onSubmit={(e) => onSubmit(e)}>
                      <div className="mb-3">
                        <input
                          type="email"
                          className="form-control"
                          id="emailInput"
                          placeholder="Email"
                          onChange={onChange}
                          value={values.email}
                          name="email"
                        />
                      </div>
                      <div className="mb-3">
                        <input
                          type="password"
                          className="form-control"
                          id="passwordInput"
                          placeholder="Senha"
                          onChange={onChange}
                          value={values.password}
                          name="password"
                        />
                      </div>

                      <button type="submit" className="btn signin-btn btn-lg">
                        Entrar
                      </button>
                      <div className="form-check signin-check">
                        <label className="form-check-label">
                          <input type="checkbox" className="form-check-input" />
                          Mantenha-me conectado
                        </label>
                      </div>
                      <div className="row">
                        <div className="col-6">
                          <a href="/support" className="signin-help-link">
                            Precisa de Ajuda?
                          </a>
                        </div>
                        <div className="col-6">
                          <a href="/register" className="signin-help-link">
                            Criar Nova Conta
                          </a>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
              <div className="col"></div>
            </div>
          </div>
        </div>
  );
};

export default Login;
