import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { AuthContext } from "./context/AuthProvider";

import Navbar from "./components/navbar/Navbar";
import Footer from "./components/footer/Footer";

import Home from "./pages/home/Home";
import Login from "./pages/login/Login";
import Registration from "./pages/registration/Registration";
import Support from "./pages/support/Support";
import NewSupport from "./pages/newSupport/NewSupport";
import EditSupport from "./pages/editSupport/EditSupport";
import About from "./pages/about/About";


import "./app.css";

const App = () => {
  const [token, setToken] = useState(null);
  const [tokenExpirationTime, setTokenExpirationTime] = useState();
  const [userInfo, setUserInfo] = useState();

  const login = (token, userInfo, expirationTime) => {
    setUserInfo(userInfo);
    setToken(token);

    const expiration =
      expirationTime || new Date(new Date().getTime() + 1000 * 60 * 60);

    setTokenExpirationTime(expiration);

    localStorage.setItem(
      "userData",
      JSON.stringify({
        token,
        expirationTime: expiration.toISOString(),
        userInfo: userInfo,
      })
    );
  };

  const logout = () => {
    alert("Obrigado por utilizar nossos serviços. Volte sempre.");

    setToken(null);
    setTokenExpirationTime(null);
    localStorage.removeItem("userData");
  };

  useEffect(() => {
    const storedData = JSON.parse(localStorage.getItem("userData"));

    if (
      storedData &&
      storedData.token &&
      new Date(storedData.expirationTime) > new Date()
    ) {
      login(storedData.token, new Date(storedData.expirationTime), userInfo);
    }
  }, []);

  useEffect(() => {
    if (token && tokenExpirationTime) {
      const remainingTime =
        tokenExpirationTime.getTime() - new Date().getTime();
      var logoutTimer = setTimeout(logout, remainingTime);
    } else {
      clearTimeout(logoutTimer);
    }
  }, []);

  return (
    <>
      <AuthContext.Provider
        value={{ isLoggedIn: !!token, login: login, logout: logout, userInfo: userInfo, token: token }}
      >
        <Router basename={process.env.PUBLIC_URL}>
          <Navbar />
            <Routes>
              <Route exact path="/" element={<Home />} name="home" />
              <Route exact path="/login" element={<Login />} />
              <Route exact path="/register" element={<Registration />} />
              <Route exact path="/support" element={<Support />} />
              <Route exact path="/support/new" element={<NewSupport />} />
              <Route exact path="support/edit/:_id" element={<EditSupport />} />
              <Route exact path="/about" element={<About />} />
            </Routes>
          <Footer />
        </Router>
      </AuthContext.Provider>
    </>
  );
};

export default App;
