import axios from "axios";
import React, { useEffect, useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../context/AuthProvider";

import './editSupport.css'

function initialState() {
  return { title: "", text: "" };
}

const EditSupport = () => {
  const [values, setValues] = useState(initialState);
  const authContext = useContext(AuthContext);

  const _id = window.location.toString().split("/")[5];

  const navigate = useNavigate();

  function onChange(e) {
    const { value, id } = e.target;

    setValues({
      ...values,
      [id]: value,
    });
  }

  function onSubmit(e) {
    e.preventDefault();

    const newDoc = {
      title: values.title,
      text: e.target.text.value,
    };

    axios.post(`/docs/update/${_id}`, newDoc, {headers: {"x-access-token": authContext.token}})
    .then((resp) => {
      if (!resp.data.success) return alert("Sem sucesso!");

      alert("Postagem salva com sucesso!");
      navigate("/support");
    });
  }

  useEffect(() => {
    axios.get(`http://localhost:4000/docs/${_id}`).then((resp) => {
      const doc = resp.data.listedDocs;

      setValues(doc);
    });
  }, []);

  return (
    <section id="edit-support">
      <div className="container mt-4 mb-4">
        {/* <!--Bootstrap classes arrange web page components into columns and rows in a grid --> */}
        <div className="row justify-content-md-center">
          <div className="col-md-12 col-lg-8">
            <h1 className="h2 mb-4">Novo Documento</h1>

            <form className="form-group" onSubmit={(e) => onSubmit(e)}>
              <label htmlFor="exampleFormControlInput1">
                Título da Publicação
              </label>
              <input
                type="text"
                className="form-control"
                id="title"
                value={values.title}
                onChange={onChange}
              />

              <label className="mt-3">Conteúdo da Publicação</label>
              <textarea
                id="text"
                value={values.text}
                onChange={onChange}
              ></textarea>
              <div className="row">
                <div className="col-sm-8"></div>
                <div className="col-sm-4 page-btn">
                  <button className="btn btn-secondary mt-3 mr-3" onClick={(e) => navigate('/support')}>
                     Cancelar
                  </button>
                  <button type="submit" className="btn btn-success mt-3">
                    Salvar
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default EditSupport;
