import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import { AuthContext } from "../../context/AuthProvider";


function initialState() {
  return { title: "" };
}

const NewSupport = () => {
  const [values, setValues] = useState(initialState);
  const [categories, setCategories] = useState([]);
  const [products, setProducts] = useState([]);
  const authContext = useContext(AuthContext);
  const navigate = useNavigate();

  function onChange(e) {
    const { value, id } = e.target;

    setValues({
      ...values,
      [id]: value,
    });
  }

  function onSubmit(e) {
    e.preventDefault();

    const newDoc = {
      title: values.title,
      text: e.target.text.value,
      product: e.target.product.value,
      categorie: e.target.categorie.value,
    };

    console.log(newDoc)

    axios.post("/docs", newDoc, {headers: {"x-access-token": authContext.token}})
      .then((resp) => {
        if (!resp.data.success) return alert("Sem sucesso!");

        console.log(resp.message)

        alert("Postagem salva com sucesso!");
        navigate("/support");
      })
      .catch((err) => {
        return alert("Fala na autenticação. Você está logado? \n(Erro: " + (err.response.data.message) + " )")
      })
      
  }

  useEffect(() => {
    axios.get("/categories").then((resp) => {
      const { success, data } = resp.data;

      if (!success)
        return alert("Não foi possível obter a lista de categorias.");

      setCategories(data);
    });
  }, []);

  useEffect(() => {
    axios.get("/products").then((resp) => {
      const { success, data } = resp.data;

      if (!success) return alert("Não foi possível obter a lista de produtos.");

      setProducts(data);
    });
  }, []);

  return (
    <section id="newSupport">
      <div className="container mt-4 mb-4">
        {/* <!--Bootstrap classes arrange web page components into columns and rows in a grid --> */}
        <div className="row justify-content-md-center">
          <div className="col-md-12 col-lg-8">
            <h1 className="h2 mb-4">Novo Documento</h1>

            <form className="form-group" onSubmit={(e) => onSubmit(e)}>
              <label>Título da Publicação</label>
              <input
                type="text"
                className="form-control"
                id="title"
                value={values.title}
                onChange={onChange}
              />

              <label className="mt-3">Produto</label>
              <select className="form-control" id="product" onChange={onChange} >
                {products.map((prod) => (
                  <option value={prod.title}>
                    {prod.title}
                  </option>
                ))}
              </select>

              <label className="mt-3">Categoria</label>
              <select className="form-control" id="categorie" onChange={onChange}>
                {categories.map((cat) => (
                  <option value={cat.title}>
                    {cat.title}
                  </option>
                ))}
              </select>

              <label className="mt-3">Conteúdo da Publicação</label>
              <textarea
                id="text"
                value={values.text}
                onChange={onChange}
              ></textarea>

              <button type="submit" className="btn btn-primary mt-3">
                Salvar
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default NewSupport;
