import React, { useContext, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../context/AuthProvider";

import "./registration.css";

function initialState() {
  return { userName: "", password: "", cpassword: "", email: "" };
}

const Registration = () => {
  const [values, setValues] = useState(initialState);
  const authContext = useContext(AuthContext);
  const navigate = useNavigate();

  function onChange(e) {
    const { value, name } = e.target;

    setValues({
      ...values,
      [name]: value,
    });
  }

  function onSubmit(e) {
    e.preventDefault();

    if (!values.password === values.cpassword)
      return alert("As senhãs não são iguais. Por favor repita.");
    axios
      .post("users/register", values)
      .then((resp) => {
        const { success } = resp.data;
        if (!success) alert("O usuário já existe.");
        if (success) {
          alert("Usuário registrado com sucesso!");
          navigate("/");
        }
      })
      .catch((error) => {
        console.log(error);
      });

    setValues(initialState);
  }

  return (
    <section id="registration">
      <div className="row">
        <div className="col-12 section-title">
          <h2>Bem vindo a Brilho!</h2>
        </div>
      </div>
      <div className="row register-page">
        <div className="col"></div>
        <div className="col-sm-5">
          <h5 className="mb-4">
            Agradeçemos o seu interesse em nossos produtos.
          </h5>
          <p>
            Após o cadastro você vai receber em seu e-mail um código de desconto
            exclusivo para a compra do seu próximo Controlador Brilho no
            lançamento. Fique atento!
          </p>
          <p>
            Ao se cadastrar, você aceita nossos{" "}
            <a href="/support">termos e condições.</a>
          </p>
        </div>
        <div className="col-sm-5">
          <form className="registration-form" onSubmit={(e) => onSubmit(e)}>
            <div className="mb-3">
              <input
                type="text"
                className="form-control"
                id="emailInput"
                placeholder="Seu nome e sobrenome"
                onChange={onChange}
                value={values.name}
                name="userName"
              />
            </div>
            <div className="mb-3">
              <input
                type="email"
                className="form-control"
                id="emailInput"
                placeholder="Seu melhor e-mail"
                onChange={onChange}
                value={values.email}
                name="email"
              />
            </div>
            <button type="submit" className="btn btn-primary btn-lg">
              Cadastrar
            </button>
          </form>
        </div>
        <div className="col"></div>
      </div>
      <div className="row">
        <div className="col mt-5"></div>
      </div>
    </section>
  );
};

export default Registration;
